import React, { useState, useContext, useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import NuevoIngreso from '../../../components/NuevoIngreso/NuevoIngreso';
import clsx from 'clsx';
import {
  Box,
  Button,
  Card,
  CardContent,
  TextField,
  InputAdornment,
  SvgIcon,
  makeStyles
} from '@material-ui/core';
import { Search as SearchIcon } from 'react-feather';
import DeleteIcon from '@material-ui/icons/Delete';
import { ProductosContext } from '../../../context/ProductosContext';
import { IngresoContext } from '../../../context/IngresoContext';
import PrintIcon from '@material-ui/icons/Print';
import { useReactToPrint } from 'react-to-print';
import ImpresionOrden from '../../../components/ImpresionOrden/ImpresionOrden';

const useStyles = makeStyles((theme) => ({
  root: {},
  importButton: {
    marginRight: theme.spacing(1)
  },
  exportButton: {
    marginRight: theme.spacing(1)
  }
}));

const BuscadorIngresos = ({ className, ...rest }) => {
  const classes = useStyles();

  const {
    setOrdenesTemp,
    ordenes,
    isNew,
    setIsNew,
    datosImpresion,
    EventoImprimirReact,
    eliminarOrden,
    componentRef
  } = useContext(IngresoContext);

  // const [ordenImprimir, setOrdenImprimir] = useState(datosImpresion);

  const filrarIngresos = (e) => {
    const results = ordenes.filter((orden) => {
      const itemData = orden.equipo.toUpperCase();
      const textData = e.target.value.toUpperCase();
      return itemData.indexOf(textData) > -1;
    });
    setOrdenesTemp(results);
  };

  const fn_nuevoProducto = () => {
    setIsNew(true);
  };

  const fn_guardar = () => {
    setIsNew(false);
  };

  // const componentRef = useRef();
  // const EventoImprimirReact = useReactToPrint({
  //   content: () => componentRef.current
  // });

  useEffect(() => {
    console.log('COMPONENTE', datosImpresion);
  }, [datosImpresion]);

  return (
    <div className={clsx(classes.root, className)} {...rest}>
      <Box display="flex" justifyContent="flex-end">
        {/* <div style={{ display: 'none' }}> */}
        {/* <ImpresionOrden
          datos="mychael datos"
          datosImpresion={datosImpresion}
          ref={componentRef}
        /> */}
        {/* </div> */}

        {isNew ? null : (
          <Button
            variant="contained"
            color="secondary"
            className={classes.exportButton}
            startIcon={<PrintIcon />}
            onClick={EventoImprimirReact}
          >
            Imprimir
          </Button>
        )}

        {isNew ? null : (
          <Button
            variant="contained"
            style={{ backgroundColor: 'rgb(154, 0, 54)' }}
            color="secondary"
            className={classes.exportButton}
            onClick={eliminarOrden}
            startIcon={<DeleteIcon />}
          >
            Eliminar
          </Button>
        )}

        {isNew ? null : (
          <Button
            color="primary"
            variant="contained"
            onClick={fn_nuevoProducto}
          >
            Nuevo Ingreso
          </Button>
        )}
      </Box>
      <Box mt={3}>
        <Card>
          {isNew ? (
            <NuevoIngreso />
          ) : (
            <Buscador filrarIngresos={filrarIngresos} />
          )}
        </Card>
      </Box>
    </div>
  );
};

const Buscador = ({ filrarIngresos }) => {
  return (
    <CardContent>
      <Box maxWidth={500}>
        <TextField
          fullWidth
          onChange={filrarIngresos}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SvgIcon fontSize="small" color="action">
                  <SearchIcon />
                </SvgIcon>
              </InputAdornment>
            )
          }}
          placeholder="Buscar Orden"
          variant="outlined"
        />
      </Box>
    </CardContent>
  );
};

export default BuscadorIngresos;
