import React , {useContext} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Divider from '@material-ui/core/Divider';
import Grid from '@material-ui/core/Grid';

import DeleteIcon from '@material-ui/icons/DeleteRounded';
import SvgIcon from '@material-ui/core/SvgIcon';
import AddIcon from '@material-ui/icons/AddBox';
import RemoveIcon from '@material-ui/icons/RemoveCircle';
import TextareaAutosize from '@material-ui/core/TextareaAutosize';
import BotonGuardarFactura from '../../../components/BotonGuardarFactura'
import { FacturaContext } from 'src/context/FacturaContext';
const useStyles = makeStyles((theme) => ({
  container: {
    display: 'grid',
    gridTemplateColumns: 'repeat(12, 1fr)',
    gridGap: theme.spacing(0),
    fontSize: '12px'
  },
  paper: {
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    whiteSpace: 'nowrap',
    marginBottom: theme.spacing(1)
  },
  paperRight: {
    padding: theme.spacing(1),
    textAlign: 'Right',
    color: theme.palette.text.secondary,
    whiteSpace: 'nowrap',
    marginBottom: theme.spacing(1)
  },
  divider: {
    margin: theme.spacing(2, 0)
  }
}));

export default function RowFactura({ totales }) {
  const classes = useStyles();

  const { observacion, setObservacion}  = useContext(FacturaContext)

  const asignarObs =(e) => {
    setObservacion(e.target.value)
  }
  function HomeIcon(props) {
    return (
      <SvgIcon {...props}>
        <path d="fa fa-plus-circle" />
      </SvgIcon>
    );
  }

  return (
    <div>
      <Divider className={classes.divider} />

      {/* <Typography variant="subtitle1" gutterBottom>
        CSS Grid Layout:
      </Typography> */}
      <div className={classes.container}>



        <div style={{ gridColumnEnd: 'span 8', height: '24px' }} >
          <Paper className={classes.paperRight}>SUBTOTAL: </Paper>

        </div>
        <div style={{ gridColumnEnd: 'span 4' }}>
          <Paper className={classes.paper}> $ {totales.subtotal}</Paper>
        </div>

        <div style={{ gridColumnEnd: 'span 8' }}>
          <Paper className={classes.paperRight}>IVA 12%: </Paper>
        </div>
        <div style={{ gridColumnEnd: 'span 4' }}>
          <Paper className={classes.paper}> $ {totales.iva}</Paper>
        </div>

        <div style={{ gridColumnEnd: 'span 8' }}>
          <Paper className={classes.paperRight}>TOTAL: </Paper>
        </div>
        <div style={{ gridColumnEnd: 'span 4' }}>
          <Paper className={classes.paper}> $ {totales.total}</Paper>
        </div>


        <div style={{ gridColumnEnd: 'span 9' }}>
          <Paper className={classes.paperRight} style={{ 'textAlign': 'left' }}>
            <TextareaAutosize defaultValue={observacion} onChange={asignarObs} style={{ width: '100%' }} aria-label="minimum height" rows={3} placeholder="Observaciones" />
          </Paper>
        </div>

        <div style={{ gridColumnEnd: 'span 3', textAlign: 'center' }}  >
          <BotonGuardarFactura />
        </div>

      </div>
    </div>
  );
}
