import React, { useState, useContext } from 'react';
import Autocomplete from '@material-ui/lab/Autocomplete';
import TextField from '@material-ui/core/TextField';
import { ClienteContext } from '../../context/ClienteContext';

const SelectCliente = ({ ancho = 300, concatenarCedula = false }) => {
  const { clientes, currentCliente, setCurrentCliente } =
    useContext(ClienteContext);

  return (
    //     <Autocomplete
    //     options={clientes}
    //     getOptionLabel={(option) => option.nombres}
    //     id="debug"
    //     debug
    //     renderInput={(params) => <TextField {...params} label="debug" margin="normal" />}
    //   />

    <Autocomplete
      id="debug"
      options={clientes}
      debug
      onChange={(event, newValue) => {
        console.log(newValue);
        if (newValue === null) return;
        setCurrentCliente(newValue);
      }}
      getOptionLabel={(option) =>
        concatenarCedula
          ? 'CI: ' + option.cedula + ' - ' + option.nombres
          : option.nombres
      }
      style={{ width: 300 }}
      renderInput={(params) => (
        <TextField
          {...params}
          style={{ width: ancho }}
          label="Seleccione Cliente"
          variant="outlined"
        />
      )}
    />
  );
};

export default SelectCliente;
